html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #FFF3ED;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #412A4C;
  font-size: 3rem;
  text-align: center;
}

.contenedor-sm .descripcion-pagina {
  color: #E57CD8;
  font-weight: 900;
}

/** Media Queries **/
.contenedor-sm {
  background-color: #FCE5D8;
}

.formulario {
  margin-top: 5rem;
  margin-bottom: 2rem;
  margin-left: 2rem;
  margin-right: 2rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  color: #412A4C;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #FFEBBD;
  color: #412A4C;
}

.acciones {
  padding: 2rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #412A4C;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #FF9CA1;
  border-left: 0.5rem, solid #FF505A;
  color: #FF505A;
}
.alerta.exito {
  background-color: #CAFCC0;
  border-left: 0.5rem, solid #14B33C;
  color: #14B33C;
}

.login {
  margin-top: 3rem;
}
.login .uptask {
  background: linear-gradient(to right, #E57CD8 0%, #a72196 100%);
  color: transparent;
  background-clip: text;
  font-size: 6rem;
  text-align: center;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 7.5rem;
  }
}
.login input[type=submit] {
  border-radius: 2.6rem;
  background-color: #E57CD8;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #dd51cb;
  cursor: pointer;
}
.login .acciones a {
  color: #412A4C;
}
.login .acciones a:hover {
  cursor: pointer;
  color: #E57CD8;
}

