.dashboard {
  background-color: #FEF9F7;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
}
.dashboard .contenido {
  padding: 3rem;
}
.dashboard .contenido .contenedor-sm {
  background-color: #FEF9F7;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-transform: uppercase;
  text-align: center;
  color: #AD50A3;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
  border: 1px solid #A3A3A3;
  border-radius: 0.5rem;
}
.dashboard .formulario input[type=submit] {
  background-color: rgb(221, 111, 209);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 1rem;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .dashboard .formulario input[type=submit] {
    width: auto;
  }
}
.dashboard .formulario input[type=submit]:hover {
  background-color: #d346c4;
  cursor: pointer;
}

.sidebar {
  background-color: #2c1338;
  padding: 2rem;
}
.sidebar h2 {
  color: #D164C5;
  margin: 0;
  text-align: center;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FDE5DC;
  display: block;
  margin-bottom: 1.5rem;
  padding: 0.7rem;
  transition-property: color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-size: 1.3rem;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  color: white;
  background-color: #351743;
  border-radius: 0.5rem;
}

.barra {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra p {
  margin: 0;
  font-size: 1.6rem;
}
.barra p span {
