.panel {
  position: relative;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  flex-direction: column;
  background: rgb(255, 255, 255);
  box-shadow: rgba(235, 231, 235, 0.5) 0px 6px 8px 0px;
  border-radius: 24px;
  width: min(664px, 100% - 50px);
  height: 462px;
  margin: 80px auto 0px;
}
.panel .no-proyectos a {
  background-color: rgb(221, 111, 209);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 1.4rem;
  padding: 1rem;
  border-radius: 0.5rem;
  display: inline-block;
}
@media (min-width: 768px) {
  .panel .no-proyectos a {
    width: auto;
  }
}
.panel .no-proyectos a:hover {
  background-color: #d346c4;
  cursor: pointer;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 5rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  background-color: #f5d6f2;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 1rem;
}
.listado-proyectos .proyecto:hover {
  background-color: #da63cd;
}
.listado-proyectos .proyecto a {
  color: #2C1338;
  text-transform: uppercase;
}

