@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$fuente_headings;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
}
h1 {
    font-size: 6rem;
}
h2 {
    font-size: 5rem;
}
h3 {
    font-size: 4rem;
}
p {
    font-size: 2rem;
    line-height: 1.8;
}

.descripcion-pagina {
    text-align: center;
}

.tagline {
    font-family: v.$fuente_headings;
    font-weight: 900;
    color:#412A4C;
    font-size: 3rem;
    text-align: center;
}

.contenedor-sm {
    .descripcion-pagina {
        color: #E57CD8;
        font-weight: 900;
    }
}